/* eslint-disable import/no-extraneous-dependencies */
/** @jsx jsx */
import { Value } from '@ali/4ever-cangjie';
import {
  renderEditor,
  getHotkeyEvent,
  fireEvent,
  jsx,
} from '@ali/4ever-dev-test';
import { waitFor, wait } from '@testing-library/react';
import BiEditor from '../editor';
import { removeInvisibleChars } from '../helper';
import { ToolbarLayout } from '@ali/we-design-next';

let origin_value
beforeAll(() => {
  origin_value = process.env.RUNTIME_ENV;
  process.env.RUNTIME_ENV = ToolbarLayout.UITEST_FLAG
})

afterAll(() => {
  process.env.RUNTIME_ENV = origin_value;
})

function clickToolbar(container, selector) {
  const quoteBlockElement = container.querySelector(selector);
  fireEvent.click(quoteBlockElement);
}

const olListData = {
  list: {
    listId: 'dn7ncrq6m7j',
    level: 0,
    isOrdered: true,
    listStyleType: 'DEC_LEN_LROM_P',
  },
};

describe('有序列表', () => {
  [
    {
      value: ((
        <value>
          <document>
            <block
              type="paragraph"
              data={olListData}
            >
              aaa
            </block>
            <block
              type="paragraph"
              data={olListData}
            >
              bbb
            </block>
            <block type="paragraph">
              ccc
              <cursor />
            </block>
          </document>
        </value>
      ) as unknown) as Value,
    },
    {
      value: ((
        <value>
          <document>
            <block
              type="paragraph"
              data={olListData}
            >
              aaa
            </block>
            <block
              type="paragraph"
              data={olListData}
            >
              bbb
            </block>
            <block type="paragraph" />
            <block type="paragraph">
              ccc
              <cursor />
            </block>
          </document>
        </value>
      ) as unknown) as Value,
    },
  ].forEach(({ value }, index) => {
    it(`点击按钮设置为有序列表时，往前查找若存在列表，则编号从对应列表最后开始算起，case ${index +
      1}`, async () => {
      const { container, content } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );
      clickToolbar(container, '[data-testid="bi-toolbar-ollist"] .wdn-toolbar-split-left');
      await waitFor(() => {
        const listItems = content.querySelectorAll('[data-testid=list]');
        const listItem = listItems[listItems.length - 1];
        expect(listItem?.dataset.start).toBe('3');
        expect(listItem.textContent).toBe('3.ccc');
      });
    });
  });

  it('点击按钮设置为有序列表时，往前查找若与前面列表间存在非空项，则编号从 1 开始算起', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={olListData}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={olListData}
          >
            bbb
          </block>
          <block type="paragraph">ccc</block>
          <block type="paragraph">
            ddd
            <cursor />
          </block>
        </document>
      </value>
    ) as unknown) as Value;
    const { container, content } = await renderEditor(
      <BiEditor defaultValue={value} />,
    );

    clickToolbar(container, '[data-testid="bi-toolbar-ollist"] .wdn-toolbar-split-left');
    await waitFor(() => {
      const listItems = content.querySelectorAll('[data-testid=list]');
      const listItem = listItems[listItems.length - 1];
      expect(listItem?.dataset.start).toBe('1');
      expect(listItem.textContent).toBe('1.ddd');
    });
  });

  it('点击按钮设置为有序列表时，往前查找若无列表，则往后查找，若后方存在列表，则与其合并', async () => {
    const value = ((
      <value>
        <document>
          <block type="paragraph">
            <cursor />
            aaa
          </block>
          <block
            type="paragraph"
            data={olListData}
          >
            bbb
          </block>
          <block
            type="paragraph"
            data={olListData}
          >
            ccc
          </block>
        </document>
      </value>
    ) as unknown) as Value;
    const { container, content } = await renderEditor(
      <BiEditor defaultValue={value} />,
    );

    clickToolbar(container, '[data-testid="bi-toolbar-ollist"] .wdn-toolbar-split-left');
    await waitFor(() => {
      const listItems = content.querySelectorAll('[data-testid=list]');
      expect(listItems[0]?.dataset.start).toBe('1');
      expect(listItems[1]?.dataset.start).toBe('2');
      expect(listItems[2]?.dataset.start).toBe('3');
    });
  });

  [
    {
      value: ((
        <value>
          <document>
            <block type="paragraph">
              <cursor />
              aaa
            </block>
            <block type="paragraph" />
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
              }}
            >
              bbb
            </block>
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
              }}
            >
              ccc
            </block>
          </document>
        </value>
      ) as unknown) as Value,
    },
    {
      value: ((
        <value>
          <document>
            <block type="paragraph">
              <cursor />
              aaa
            </block>
            <block type="paragraph">bbb</block>
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
              }}
            >
              ccc
            </block>
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
              }}
            >
              ddd
            </block>
          </document>
        </value>
      ) as unknown) as Value,
    },
  ].forEach(({ value }, index) => {
    it(`点击按钮设置为有序列表时，往前查找若无列表，则往后查找，若与后方列表间存在项，则不与其合并，case ${index +
      1}`, async () => {
      const { container, content } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );

      clickToolbar(container, '[data-testid="bi-toolbar-ollist"] .wdn-toolbar-split-left');
      await waitFor(() => {
        const listItems = content.querySelectorAll('[data-testid=list]');
        expect(listItems[0]?.dataset.start).toBe('1');
        expect(listItems[0].textContent).toBe('1.aaa');
        expect(listItems[1]?.dataset.start).toBe('1');
        expect(listItems[2]?.dataset.start).toBe('2');
      });
    });
  });

  it('插入一项时，后续同嵌套层级的标号需要更新', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
            <cursor />
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 1, isOrdered: true },
            }}
          >
            bbb
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            ccc
          </block>
        </document>
      </value>
    ) as unknown) as Value;
    const { content } = await renderEditor(<BiEditor defaultValue={value} />);

    fireEvent.keyDown(content, getHotkeyEvent('enter'));
    // await wait();
    await waitFor(() => {
      const listItems = content.querySelectorAll('[data-testid=list]');
      const lastItem = listItems[listItems.length - 1];
      expect(lastItem?.dataset.start).toBe('3');
      expect(lastItem.textContent).toBe('3.ccc');
      expect(listItems[0]?.dataset.start).toBe('1');
      expect(listItems[1]?.dataset.start).toBe('2');
    });
  });

  it('删除列表中间一项，后续同层级的列表序号要自动更新', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            <cursor />
            bbb
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            ccc
          </block>
        </document>
      </value>
    ) as unknown) as Value;

    const { content } = await renderEditor(<BiEditor defaultValue={value} />);

    fireEvent.keyDown(content, getHotkeyEvent('backspace'));
    await waitFor(() => {
      const listItems = content.querySelectorAll('[data-testid=list]');
      const lastItem = listItems[listItems.length - 1];
      expect(lastItem?.dataset.start).toBe('2');
      expect(lastItem.textContent).toBe('2.ccc');
      expect(listItems[0]?.dataset.start).toBe('1');
    });
  });
  it('删除列表中间一项，后续嵌套层级的列表序号保持不变', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            <cursor />
            bbb
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 1, isOrdered: true },
            }}
          >
            ccc
          </block>
        </document>
      </value>
    ) as unknown) as Value;

    const { content } = await renderEditor(<BiEditor defaultValue={value} />);

    fireEvent.keyDown(content, getHotkeyEvent('backspace'));
    await waitFor(() => {
      const listItems = content.querySelectorAll('[data-testid=list]');
      const lastItem = listItems[listItems.length - 1];
      expect(lastItem?.dataset.start).toBe('1');
      expect(lastItem.textContent).toBe('a.ccc');
      expect(listItems[0]?.dataset.start).toBe('1');
    });
  });
  it('listId 相同时，表格外编辑列表，表格内的列表序号不变', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            bb<cursor />b
          </block>
          <block type="table" data={{ colsWidth: [100, 100] }}>
            <block type="table-row">
              <block type="table-cell">
                <block
                  type="paragraph"
                  data={{
                    list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
                  }}
                >
                  ccc
                </block>
              </block>
              <block type="table-cell">
                <block type="paragraph">
                  ddd
                </block>
              </block>
            </block>
          </block>
        </document>
      </value>
    ) as unknown) as Value;
    const { content, editor } = await renderEditor(<BiEditor defaultValue={value} />);
    fireEvent.cangjieInput(editor, { data: ' ' });
    await wait();

    const listItems = content.querySelectorAll('[data-testid=list]');
    const lastItem = listItems[listItems.length - 1];
    expect(lastItem?.dataset.start).toBe('1');
    expect(lastItem.textContent).toBe('1.ccc');
    expect(listItems[0]?.dataset.start).toBe('1');
  });
  it('listId相同时，表格外列表新增一项，表格内的列表序号不变化', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
            <cursor />
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            bbb
          </block>
          <block type="table" data={{ colsWidth: [100, 100] }}>
            <block type="table-row">
              <block type="table-cell">
                <block
                  type="paragraph"
                  data={{
                    list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
                  }}
                >
                  ccc
                </block>
              </block>
              <block type="table-cell">
                <block type="paragraph">
                  ddd
                </block>
              </block>
            </block>
          </block>
        </document>
      </value>
    ) as unknown) as Value;

    const { content } = await renderEditor(<BiEditor defaultValue={value} />);
    fireEvent.keyDown(content, getHotkeyEvent('enter'));
    await wait();

    const listItems = content.querySelectorAll('[data-testid=list]');
    const lastItem = listItems[listItems.length - 1];
    expect(lastItem?.dataset.start).toBe('1');
    expect(lastItem.textContent).toBe('1.ccc');
    expect(listItems[0]?.dataset.start).toBe('1');
  });
  it('listId相同时，表格外列表删除一项，表格内的列表序号不变化', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            <cursor />
            bbb
          </block>
          <block type="table" data={{ colsWidth: [100, 100] }}>
            <block type="table-row">
              <block type="table-cell">
                <block
                  type="paragraph"
                  data={{
                    list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
                  }}
                >
                  ccc
                </block>
              </block>
              <block type="table-cell">
                <block type="paragraph">
                  ddd
                </block>
              </block>
            </block>
          </block>
        </document>
      </value>
    ) as unknown) as Value;

    const { content } = await renderEditor(<BiEditor defaultValue={value} />);

    fireEvent.keyDown(content, getHotkeyEvent('backspace'));
    await wait();
    const listItems = content.querySelectorAll('[data-testid=list]');
    const lastItem = listItems[listItems.length - 1];
    expect(lastItem?.dataset.start).toBe('1');
    expect(lastItem.textContent).toBe('1.ccc');
    expect(listItems[0]?.dataset.start).toBe('1');
  });
  [
    {
      title: '把有序列表项改为普通段落，后续更高嵌套层级的标号需要更新',
      action: async (container) => {
        clickToolbar(container, '[data-testid="bi-toolbar-ollist"] .wdn-toolbar-split-left');
      },
      expectedOrder: 2,
    },
  ].forEach(({ title, action, expectedOrder }) => {
    it(title, async () => {
      const value = ((
        <value>
          <document>
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
              }}
            >
              aaa
            </block>
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 1, isOrdered: true },
              }}
            >
              bbb
            </block>
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
              }}
            >
              <cursor />
              ccc
            </block>
            <block
              type="paragraph"
              data={{
                list: { listId: 'dn7ncrq6m7j', level: 1, isOrdered: true },
              }}
            >
              ddd
            </block>
          </document>
        </value>
      ) as unknown) as Value;
      const { container, content } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );

      await action(container);
      await waitFor(() => {
        const listItems = content.querySelectorAll('[data-testid=list]');
        const lastItem = listItems[listItems.length - 1];
        expect(lastItem.dataset.start).toBe(`${expectedOrder}`);
        expect(lastItem.textContent).toBe('b.ddd');
      });
    });
  });

  it('同层级列表', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
        </document>
      </value>
    ) as unknown) as Value;

    const { content } = await renderEditor(
      <BiEditor defaultValue={value} />,
    );

    await waitFor(() => {
      const listsElement = content.querySelectorAll('[data-testid=list]');
      listsElement.forEach((list, i) => {
        const start = list?.dataset.start;
        expect(start).toBe(`${i + 1}`);
      });
    });
  });
  it('列表递增层级', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 1, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 2, isOrdered: true },
            }}
          >
            aaa
          </block>
        </document>
      </value>
    ) as unknown) as Value;

    const { content } = await renderEditor(
      <BiEditor defaultValue={value} />,
    );

    await waitFor(() => {
      const listsElement = content.querySelectorAll('[data-testid=list]');
      listsElement.forEach((list) => {
        const start = list?.dataset.start;
        expect(start).toBe('1');
      });
    });
  });
  it('列表递减层级', async () => {
    const value = ((
      <value>
        <document>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 2, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 1, isOrdered: true },
            }}
          >
            aaa
          </block>
          <block
            type="paragraph"
            data={{
              list: { listId: 'dn7ncrq6m7j', level: 0, isOrdered: true },
            }}
          >
            aaa
          </block>
        </document>
      </value>
    ) as unknown) as Value;

    const { content } = await renderEditor(
      <BiEditor defaultValue={value} />,
    );

    await waitFor(() => {
      const listsElement = content.querySelectorAll('[data-testid=list]');
      listsElement.forEach((list, i) => {
        const start = list?.dataset.start;
        if (i > 0) {
          expect(start).toBe('2');
        } else {
          expect(start).toBe('1');
        }
      });
    });
  });
  describe('非空段落，不响应列表的插入符号', () => {
    it('非空段落，不响应列表的插入符号', async () => {
      const value = ((
        <value>
          <document>
            <block type="paragraph">
              xxx <cursor />
            </block>
          </document>
        </value>
      ) as unknown) as Value;
      const { content, editor } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );

      fireEvent.cangjieInput(editor, { data: '*' });
      fireEvent.keyDown(content, getHotkeyEvent('space'));
      // await wait();
      await waitFor(() => {
        expect(content.textContent).toEqual('xxx *');
      });
    });

    it('若不在段落第一个纯文本节点，则不响应列表的插入符号', async () => {
      const value = ((
        <value>
          <document>
            <block type="paragraph">
              <inline
                type="link"
                data={{ href: 'https://www.dingtalk.com' }}
              />
              <cursor />
            </block>
          </document>
        </value>
      ) as unknown) as Value;
      const { content, editor } = await renderEditor(
        <BiEditor defaultValue={value} />,
      );

      fireEvent.cangjieInput(editor, { data: '*' });
      fireEvent.keyDown(content, getHotkeyEvent('space'));
      await waitFor(() => {
        expect(removeInvisibleChars(content.textContent ?? '')).toEqual('*');
      });
    });
  });
});